"use client";

import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery } from "@tanstack/react-query";
import { useSearchParams } from "next/navigation";

// Helper function to format dates
const formatDate = (dateString: string) => {
  const date = new Date(dateString);
  return date.toLocaleDateString("en-US", {
    year: "numeric",
    month: "long",
    day: "numeric",
  });
};

// Helper function to get month name
const getMonthName = (monthNumber: number) => {
  const date = new Date();
  date.setMonth(monthNumber - 1);
  return date.toLocaleString("en-US", { month: "long" });
};

export default function InfantDetails() {
  const searchParams = useSearchParams();
  const id = searchParams.get("id");
  const { getOneInfantDetail } = useProtectedRoutesApi();

  const { data, isLoading, isError, error } = useQuery({
    queryKey: ["infant", id],
    queryFn: () => getOneInfantDetail(id as string),
  });

  if (isLoading) return <div>Loading...</div>;
  if (isError) return <div>Error: {error.message}</div>;

  const infantData = data?.data;
  const address = infantData?.address;
  const birthday = infantData?.birthday;
  const parent = infantData?.Parent;
  const vaccinationSchedule = infantData?.Vaccination_Schedule;

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />

      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        {/* Personal Information Section */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold mb-4">Personal Information</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="flex items-center">
              {/* eslint-disable-next-line @next/next/no-img-element */}
              <img
                src={infantData?.image}
                alt="Infant"
                className="w-32 h-32 rounded-full object-cover mr-4"
              />
              <div>
                <p className="text-xl font-semibold">{infantData.fullname}</p>
                <p className="text-gray-600">{infantData.gender}</p>
              </div>
            </div>

            <div className="space-y-2">
              <p>
                <span className="font-semibold">Height:</span>{" "}
                {infantData.height} cm
              </p>
              <p>
                <span className="font-semibold">Weight:</span>{" "}
                {infantData.weight} kg
              </p>
              <p>
                <span className="font-semibold">Health Center:</span>{" "}
                {infantData.health_center}
              </p>
              <p>
                <span className="font-semibold">Family Number:</span>{" "}
                {infantData.family_no}
              </p>
            </div>
          </div>
        </div>

        {/* Parent Information Section */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold mb-4">Parent Information</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <p>
                <span className="font-semibold">Mother's Name:</span>{" "}
                {infantData.mothers_name}
              </p>
              <p>
                <span className="font-semibold">Father's Name:</span>{" "}
                {infantData.fathers_name}
              </p>
            </div>
            <div>
              <p>
                <span className="font-semibold">Parent Email:</span>{" "}
                {parent?.auth?.email}
              </p>
            </div>
          </div>
        </div>

        {/* Address Section */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold mb-4">Address</h2>
          <p>
            {address?.purok} Purok, {address?.baranggay},{" "}
            {address?.municipality}, {address?.province}
          </p>
        </div>

        {/* Birthday Details */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold mb-4">Birthday Details</h2>
          <p>
            {getMonthName(birthday?.month)} {birthday?.day}, {birthday?.year}
          </p>
          <p className="mt-2">
            <span className="font-semibold">Place of Birth:</span>{" "}
            {infantData.place_of_birth}
          </p>
        </div>

        {/* Vaccination Schedule */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold mb-4">Vaccination Schedule</h2>
          <div className="space-y-4">
            {vaccinationSchedule?.map((schedule: any) => (
              <div key={schedule.id} className="border rounded-lg p-4">
                <h3 className="font-semibold text-lg mb-2">
                  {schedule.vaccine_names[0]?.vaccine_name}
                </h3>
                <div className="space-y-2">
                  {schedule.firstDose && (
                    <div className="flex justify-between items-center">
                      <span>1st Dose:</span>
                      <span
                        className={`badge ${
                          schedule.Vaccination[0]?.firstDoseStatus === "DONE"
                            ? "bg-green-100 text-green-800"
                            : "bg-red-100 text-red-800"
                        }`}
                      >
                        {formatDate(schedule.firstDose)} -{" "}
                        {schedule.Vaccination[0]?.firstDoseStatus || "PENDING"}
                      </span>
                    </div>
                  )}
                  {schedule.secondDose && (
                    <div className="flex justify-between items-center">
                      <span>2nd Dose:</span>
                      <span
                        className={`badge ${
                          schedule.Vaccination[0]?.secondDoseStatus === "DONE"
                            ? "bg-green-100 text-green-800"
                            : "bg-gray-100 text-gray-800"
                        }`}
                      >
                        {schedule.secondDose
                          ? formatDate(schedule.secondDose)
                          : "N/A"}{" "}
                        -{" "}
                        {schedule.Vaccination[0]?.secondDoseStatus || "PENDING"}
                      </span>
                    </div>
                  )}
                  {schedule.thirdDose && (
                    <div className="flex justify-between items-center">
                      <span>3rd Dose:</span>
                      <span
                        className={`badge ${
                          schedule.Vaccination[0]?.thirdDoseStatus === "DONE"
                            ? "bg-green-100 text-green-800"
                            : "bg-gray-100 text-gray-800"
                        }`}
                      >
                        {schedule.thirdDose
                          ? formatDate(schedule.thirdDose)
                          : "N/A"}{" "}
                        -{" "}
                        {schedule.Vaccination[0]?.thirdDoseStatus || "PENDING"}
                      </span>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      </main>

      <Footer />
    </div>
  );
}
