"use client";

import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery } from "@tanstack/react-query";
import { useSearchParams } from "next/navigation";

export default function InfantDetails() {
  const searchParams = useSearchParams();
  const id = searchParams.get("id");
  const { getOneInfantDetail } = useProtectedRoutesApi();

  // Fetch the infant details
  const { data, isLoading, isError, error } = useQuery({
    queryKey: ["infant", id],
    queryFn: () => getOneInfantDetail(id as string),
  });

  if (isLoading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="text-gray-500 text-xl">Loading infant details...</div>
      </div>
    );
  }

  if (isError) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="text-red-500 text-xl">Error: {error.message}</div>
      </div>
    );
  }

  // Ensure that data is available
  if (!data?.data) {
    return null;
  }

  // Sort the Vaccination_Schedule array by vaccine_type_code
  const sortedVaccinationSchedule = data.data.Vaccination_Schedule.slice().sort(
    (a, b) => {
      const codeA = parseInt(a.vaccine_names[0].vaccine_type_code);
      const codeB = parseInt(b.vaccine_names[0].vaccine_type_code);
      return codeA - codeB;
    }
  );

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      {/* Sticky Sidebar */}
      <Sidebar />

      {/* Main Content */}
      <main className="max-w-4xl mx-auto p-8 sm:p-20 font-sans flex flex-col gap-12 bg-gray-50">
        {/* Profile Header */}
        <div className="flex flex-col sm:flex-row items-center sm:items-start space-y-4 sm:space-y-0 sm:space-x-6">
          {data.data.image && (
            <img
              src={data.data.image}
              alt={`${data.data.fullname}'s Image`}
              className="w-32 h-32 object-cover rounded-full shadow-lg"
            />
          )}
          <div className="text-center sm:text-left">
            <h1 className="text-4xl font-bold text-gray-800">
              {data.data.fullname}
            </h1>
            <p className="text-gray-600 mt-1">ID: {data.data.id}</p>
          </div>
        </div>

        {/* Personal Information */}
        <section className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4 border-b pb-2">
            Personal Information
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-gray-700">
            <p>
              <span className="font-medium">Gender:</span> {data.data.gender}
            </p>
            <p>
              <span className="font-medium">Date of Birth:</span>{" "}
              {`${data.data.birthday.month}/${data.data.birthday.day}/${data.data.birthday.year}`}
            </p>
            <p>
              <span className="font-medium">Place of Birth:</span>{" "}
              {data.data.place_of_birth}
            </p>
            <p>
              <span className="font-medium">Height:</span> {data.data.height} cm
            </p>
            <p>
              <span className="font-medium">Weight:</span> {data.data.weight} kg
            </p>
          </div>
        </section>

        {/* Parental Information */}
        <section className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4 border-b pb-2">
            Parental Information
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-gray-700">
            <p>
              <span className="font-medium">Mother's Name:</span>{" "}
              {data.data.mothers_name}
            </p>
            <p>
              <span className="font-medium">Father's Name:</span>{" "}
              {data.data.fathers_name}
            </p>
            <p>
              <span className="font-medium">Parent ID:</span>{" "}
              {data.data.parent_id}
            </p>
            <p>
              <span className="font-medium">Family Number:</span>{" "}
              {data.data.family_no}
            </p>
            <p className="md:col-span-2">
              <span className="font-medium">Address:</span>{" "}
              {`${data.data.address.purok}, ${data.data.address.baranggay}, ${data.data.address.municipality}, ${data.data.address.province}`}
            </p>
          </div>
        </section>

        {/* Health Information */}
        <section className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4 border-b pb-2">
            Health Information
          </h2>
          <div className="text-gray-700 space-y-2">
            <p>
              <span className="font-medium">Health Center:</span>{" "}
              {data.data.health_center}
            </p>
          </div>
        </section>

        {/* Vaccination Schedule */}
        <section className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4 border-b pb-2">
            Vaccination Schedule
          </h2>
          {sortedVaccinationSchedule.map((schedule, index) => (
            <div key={index} className="mt-6 p-4 border rounded-md shadow-sm">
              <h3 className="text-xl font-semibold text-gray-700">
                {schedule.vaccine_names[0].vaccine_name}
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-2 text-gray-700">
                <p>
                  <span className="font-medium">First Dose:</span>{" "}
                  {schedule.firstDose
                    ? new Date(schedule.firstDose).toLocaleDateString()
                    : "N/A"}
                </p>
                {schedule.secondDose && (
                  <p>
                    <span className="font-medium">Second Dose:</span>{" "}
                    {new Date(schedule.secondDose).toLocaleDateString()}
                  </p>
                )}
                {schedule.thirdDose && (
                  <p>
                    <span className="font-medium">Third Dose:</span>{" "}
                    {new Date(schedule.thirdDose).toLocaleDateString()}
                  </p>
                )}
                <p>
                  <span className="font-medium">Status:</span>{" "}
                  {schedule.Vaccination[0].percentage}% completed
                </p>
                <p>
                  <span className="font-medium">Remarks:</span>{" "}
                  {schedule.Vaccination[0].remarks || "None"}
                </p>
              </div>
              {/* Progress Bar */}
              <div className="w-full bg-gray-200 rounded-full h-4 mt-4">
                <div
                  className={`h-4 rounded-full ${
                    schedule.Vaccination[0].percentage === 100
                      ? "bg-green-500"
                      : "bg-blue-500"
                  }`}
                  style={{ width: `${schedule.Vaccination[0].percentage}%` }}
                ></div>
              </div>
            </div>
          ))}
        </section>
      </main>

      {/* Sticky Footer */}
      <Footer />
    </div>
  );
}
